import common from "../../../common/common.js"
var serverUrl = common.serverUrl
export default{
	name:"UserPost",
	props:{
		post:Object,
		index:Number,
		currentUserId:String
	},
	data() {
			return {
			userId:"",
			userInfo:{},
			token:'',
			type:'',
			}

		},
		onLoad() {
			var userInfo = uni.getStorageSync("globalUser")
			if(userInfo!=null&&userInfo!=""&&userInfo!=undefined){
				this.userInfo=userInfo
				this.token="Bearer "+this.userInfo.token
				this.type=this.userInfo.tokenType
				this.userId=this.userInfo.userId
			}
		},
	methods:{
		viewMe(index){
			var current = index
			uni.previewImage({
				current:current,
				urls: this.post.imgUrls,
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						// console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		},
		edit(e){
			var postId = e.currentTarget.dataset.postid
			uni.navigateTo({
				url:'../send/PostEdit?postId='+postId
			})
		},
		deletePost(e){
			var postId = e.currentTarget.dataset.postid
			this.$emit("deletePost",postId)
		},
		love(type){
			this.$emit("love",{
				type,
				index:this.index
				})
		},
		goToPostDetail(data){
			var postStr = JSON.stringify(data)
			uni.navigateTo({
				url:'./PostDetail?postStr='+postStr
			})

		},
		follow(index){
			// 点击后触发一个事件给父组件
			this.$emit('follow',this.post.userId)
		},
		openSpace(){
			uni.navigateTo({
				url:"../userCenter/userCenter"
			})
		},

	}

}



